// Colors
$brand-white: #fff;
$brand-black: #141414;
$brand-pink: #0000ff;
// $brand-pink: #ff4092;
$brand-purple: #7b1173;
$brand-violet: #e000cf;
$brand-blue: #11597b;

$brand-grey: #41474e;
$brand-grey-light: #8b98a9;
$brand-grey-dark: #303030;
$brand-grey-lighter: #e2e2e2;

$green: #5fb359;
$red: #d80606;
$orange: #b35f36;
$yellow: #eac146;

$brand-gradient: linear-gradient(to right bottom, $brand-black, $brand-black); // was purple to pink.

$body-background: darken($brand-white, 1%);

// Fonts
$font-family-base: sans-serif;
$font-family-title: sans-serif;
$font-family-monospace: 'Courier New', monospace;
// $font-family-base: 'Sharp Sans', -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif;
// $font-family-title: 'Sharp Sans Display', -apple-system, BlinkMacSystemFont, 'Segoe UI', Helvetica, Arial, sans-serif;
// $font-family-monospace: 'Fira Code', 'Fira Mono', Menlo, Monaco, Consolas, 'Courier New', monospace;

$font-size-root: 16px;
$font-size-base: 1rem;
$font-size-large: 1.2rem;
$font-size-small: .85rem;
$font-size-mini: .65rem;
$font-size-text: $font-size-base;
$font-size-label: $font-size-base;
$font-size-title: 1.4rem;
$font-size-h1: 3rem;
$font-size-h2: 1.7rem;
$font-size-h3: 1.4rem;
$font-size-h4: $font-size-large;
$font-size-h5: 1.1rem;

$font-weight-base: 500;
$font-weight-bold: 600;

$line-height: 1.6;

// Sizes
$spacer: 2rem;
$page-frame: .75rem;

$break-point--small: 640px;
$break-point--medium: 860px;
$break-point--large: 1140px;
$break-point--huge: 1400px;
$brand-border-width: 1px;

$border-radius: .2rem;

$narrowWidth: 35rem;
